import Vue from "vue";
import Router from "vue-router";
import Tarifs from './views/Tarifs.vue';
import AllTarifs from "./views/AllTarifs.vue";
import TarifOptions from './views/TarifOptions.vue';
import TarifConnect from './views/TarifConnect.vue';

Vue.use(Router);

const router = new Router({
  mode: 'history',
  base: getLocationRoot(),
  routes: [
    {
      path: '/',
      redirect: '/tarifs'
    },
    {
      path: '/tarifs',
      name: 'tarifs',
      redirect: { name: 'all_tarifs' },
      component: Tarifs,
      children: [
        {
          path: 'all',
          name: 'all_tarifs',
          component: AllTarifs,
          meta: { title: 'Выбирай', isRoot: true }
        },
        {
          path: 'all/:title/:options',
          name: 'tarif_options',
          props: true,
          component: TarifOptions,
          meta: { titleDynamic: (route) => `Тариф "${route.params.title}"` }
        },
        {
          path: 'all/connect/:title/:data',
          name: 'tarif_connect',
          props: true,
          component: TarifConnect,
          meta: { title: 'Выбор тарифа' }
        }
      ]
    },
  ],
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
});

router.beforeEach((to, from, next) => {
  if (to.meta.titleDynamic) {
    to.meta.title = to.meta.titleDynamic(to);
  }
  next()
})

function getLocationRoot() {
  return location.pathname.substr(0, location.pathname.indexOf('/tarifs'))
}

export default router;
