<template>
  <Card flexible>
    <h3 slot='head' class='title'>
      Тариф "{{title}}"
    </h3>
    <div slot='body' class='body'>
      <div :style='{background: labelColor}' class='speed_bg'>
        <h4 class='speed'>{{speed}} Мбит/с</h4>
      </div>
      <h4 class='price'>
        {{priceRange.min}} - {{priceRange.max}} ₽/мес
      </h4>
      <div v-if='freeOptions' class='options'>
        <p v-for='(option, i) in freeOptions'
          :key='i'
          class='option'>
          {{option}}
        </p>
      </div>
      <router-link
        :to="{
          name: 'tarif_options',
          params: { title, options: JSON.stringify(tarifOptions) }
        }"
        class='router_button' />
      <icn-arrow class='route-icn' color='#CECED2' />
    </div>
    <div slot='foot' class='foot'>
      <a :href='link' class='link'>
        узнать подробнее на сайте www.sknt.ru
      </a>
    </div>
  </Card>
</template>

<script>
import Card from '@/components/UI/Card';

export default {
  props: {
    title: String,
    link: String,
    speed: Number,
    freeOptions: Array,
    tarifOptions: Array,
    labelColor: {
      type: String,
      default: '#0380DE'
    }
  },
  computed: {
    priceRange() {
      const allPrices = this.tarifOptions.map(item => item.price / item.pay_period);
      return {
        min: Math.min(...allPrices),
        max: Math.max(...allPrices)
      };
    }
  },
  components: { Card }
}
</script>

