export default {
  get(url) {
    return new Promise((resolve, reject) => {
      const xhr = new XMLHttpRequest();
      xhr.onreadystatechange = () => {
        if (xhr.readyState === 4 && xhr.status === 200) {
          resolve(xhr.response);
        }
        if (xhr.readyState === 4 && xhr.status !== 200) {
          reject(xhr.statusText);
        }
      };
      xhr.open('GET', url);
      xhr.send();
    });
  }
};
