<template>
  <div class="tarif_connect">
    <Card flexible>
      <h3 slot='head'>
        Тариф "{{title}}"
      </h3>
      <div slot='body' class='body'>
        <div class="text_block">
          <h4> Период оплаты — {{data.pay_period}} месяцев</h4>
          <h4> {{data.price / data.pay_period}} ₽/мес</h4>
        </div>
        <div class="text_block">
          <p>разовый платеж — {{data.price}} ₽</p>
          <p>со счета спишется — {{data.price}} ₽</p>
        </div>
        <div class="text_block text_gray">
          <p>вступит в силу — сегодня</p>
          <p>активно до {{date}}</p>
        </div>
      </div>  
      <div slot='foot' class='foot'>
        <Button fluid>Выбрать</Button>
      </div>
    </Card>
  </div>
</template>

<script>
import Card from '@/components/UI/Card';
import Button from '@/components/UI/Button';

export default {
  props: {
    title: String,
    data: Object
  },
  computed: {
    date() {
      const timestamp = String(this.data.new_payday).split('+')[0];
      return new Date(timestamp * 1000).toLocaleDateString();
    }
  },
  components: { Card, Button }
};
</script>

