const COLORS = [
  { name: 'Земля', color:'#7B6B47' },
  { name: 'Огонь', color:'#EA5205' },
  { name: 'Вода', color:'#0380DE' },
  { name: 'Вода HD', color:'#0380DE' },
  { name: 'Огонь HD', color:'#EF3306' }
]

export function addColors(data) {
  return data.map(item => {
    const { color } = COLORS.find(color => color.name === item.title);
    return {...item, label_color: color}
  })
}