<template>
  <div class="tarif_options row">
    <div v-for="option in optionsNormalized"
      :key='option.title'
      class='tarif-col col-sm-12 col-md-6'>
      <keep-alive>
      <Card flexible>
        <h3 slot='head'>
          {{option.title}}
        </h3>
        <div slot='body' class='body'>
          <h4 class='price'>
            {{option.price / option.pay_period}} ₽/мес
          </h4>
          <div class="info">
          <p>разовый платеж - {{option.price}} ₽</p>
          <p v-if='option.discount !== 0'>скидка - {{option.discount}} ₽</p>
          </div>
          <router-link
            :to="{ name: 'tarif_connect', params: { title, data: option } }"
            class='router_button' />
          <icn-arrow class='route-icn' color='#CECED2' />
        </div>
      </Card>
      </keep-alive>
    </div>
  </div>
</template>

<script>
import Card from '@/components/UI/Card';

export default {
  props: {
    title: String,
    options: String
  },
  computed: {
    optionsNormalized() {
      const sorted = JSON.parse(this.options).sort((a, b) => a.ID - b.ID)
      return sorted.map(t => {
          return {
            ...t,
            discount: (sorted[0].price - t.price / t.pay_period) * t.pay_period
          }
        })
    }
  },
  components: { Card }
};
</script>

