<template>
  <div class="all_tarifs">
    <div class='row'>
      <div 
        v-for="tarif in tarifs"
        :key='tarif.title'
        class="tarif-col col-sm-12 col-md-6">
          <TarifUnit 
            :title='tarif.title'
            :link='tarif.link'
            :speed='tarif.speed'
            :tarifOptions='tarif.tarifs'
            :freeOptions='tarif.free_options'
            :labelColor='tarif.label_color'
          />
      </div>
    </div>
  </div>
</template>

<script>
import TarifUnit from '@/components/TarifUnit';
import * as API from '@/API';

export default {
  data() {
    return {
      tarifs: [],
      error: false,
      loading: false
    }
  },
  mounted() {
    this.fetchTarifs();
  },
  methods: {
    async fetchTarifs() {
      try {
        this.tarifs = await API.getTarifs();
        window.global.tarifs = this.tarifs;
      }
      catch (e) {
        this.error = true;
      }
    }
  },
  components: { TarifUnit }
};
</script>

