<template>
  <div id="app">
    <Head
      @backClick='handleBackClick'
      :backButton='backButtonShow'
      :title="routeName" />
    <div class='app_body'>
      <router-view />
    </div>
  </div>
</template>

<script>
import Head from '@/components/Head';

export default {
  components: { Head },
  computed: {
    routeName() {
      return this.$route.meta.title;
    },
    backButtonShow() {
      return !this.$route.meta.isRoot;
    }
  },
  methods: {
    handleBackClick() {
      this.$router.go(-1);
    }
  }
};
</script>

<style lang="scss">
@import './styles/global.scss';
@import './styles/typography.scss';
@import './styles/preventDefaults.scss';

#app {
  height: 100%;
  width: 100%;
  font-family: sans-serif;
  font-size: 16px;
}
