import { addColors } from './normalize';
import http from './httpClient';
import dataMock from './__mock__/tarifsMock.json'

const API_KEY = 'http://shirass.000webhostapp.com/'

export async function getTarifs() {
  try {
    const data = await http.get(API_KEY + 'get-tarifs.php');
    const { tarifs } = JSON.parse(data);
    return addColors(tarifs);
  } catch (e) {
    console.error(e);
    return addColors(dataMock.tarifs);
  }
}